<template>
  <div class="mapwrap">
    <div id="map"></div>
    <div class="gu-select">
      <b-form-select
        @change="setCurrentGu"
        v-model="currentGu"
        :options="options"
      ></b-form-select>

      <b-button
        size="sm"
        variant="primary"
        @click="getCurrentGu"
        class="markerbtn"
      >
          </b-button
      ><b-button size="sm" @click="emptyMarkers" class="markerbtn"
        > </b-button
      >
    </div>
    <div class="popuparea">
      <button class="listclose" @click="toggleGuSelected" v-show="!guSelected">
        <strong>></strong>
      </button>
      <house-list
        v-show="guSelected"
        @isGuSelected="isGuSelected"
        class="popup"
        style="margin-top: 0; overflow: hidden; padding-top: 15px"
      ></house-list>
      <house-detail v-if="house" class="popup"></house-detail>
    </div>

    <ul id="category">
      <li id="BK9" data-order="0">
        <span class="category_bg bank"></span>
        
      </li>
      <li id="MT1" data-order="1">
        <span class="category_bg mart"></span>
        
      </li>
      <li id="PM9" data-order="2">
        <span class="category_bg pharmacy"></span>
        
      </li>
      <li id="OL7" data-order="3">
        <span class="category_bg oil"></span>
        
      </li>
      <li id="CE7" data-order="4">
        <span class="category_bg cafe"></span>
        
      </li>
      <li id="CS2" data-order="5">
        <span class="category_bg store"></span>
        
      </li>
    </ul>
    <amenity-detail
      class="amenitypopup"
      v-if="amenity"
      style="padding: 10px"
    ></amenity-detail>
  </div>
</template>
<script>
//import axios from "axios";
import http from "@/api/http";
import HouseDetail from "@/components/house/HouseDetail.vue";
import HouseList from "@/components/house/HouseList.vue";
import AmenityDetail from "@/components/house/AmenityDetail.vue";
import { mapMutations } from "vuex";
import { mapState } from "vuex";
import latLngs from "@/utils/guLatLng";
//import { mapGetters } from "vuex";
export default {
  name: "HouseMap",
  components: {
    HouseDetail,
    HouseList,
    AmenityDetail,
  },
  data() {
    return {
      currentGu: null,
      currentDong: null,
      aptListCenter: null,
      guList: [],
      options: [],
      markers: [],
      aptSelected: false,
      guSelected: false,
      infowindow: null,
      geocoder: null,
      map: null,
      ps: null,
      contentNode: null,
      clusterer: null,
      // placeOverlay: null,
      currCategory: "",
      amenitymarkers: [],
    };
  },
  computed: {
    ...mapState("houseStore", ["house", "houselist", "center", "amenity"]),
  },
  methods: {
    isGuSelected() {
      this.guSelected = false;
    },
    toggleGuSelected() {
      this.guSelected = !this.guSelected;
    },
    ...mapMutations("houseStore", [
      "SET_DETAIL_HOUSE",
      "SET_HOUSE_DEALS",
      "SET_HOUSE_LIST",
      "SET_HOUSE_DEALS_YEARLY",
      "SET_DETAIL_AMENITY",
    ]),
    initMap() {
      const container = document.getElementById("map");
      const options = {
        center: new kakao.maps.LatLng(37.5666805, 126.9784147),
        level: 4,
      };

      this.map = new kakao.maps.Map(container, options);
      this.ps = new kakao.maps.services.Places(this.map);
      this.geocoder = new kakao.maps.services.Geocoder();
      //   
      // kakao.maps.event.addListener(this.map, "idle", () => {
      //   this.searchAddrFromCoords(this.map.getCenter(), this.displayCenterInfo);
      // });
      this.SET_DETAIL_AMENITY(null);
      this.contentNode = document.createElement("div");
      this.addCategoryClickEvent();
      kakao.maps.event.addListener(this.map, "idle", this.searchPlaces);
      var zoomControl = new kakao.maps.ZoomControl();
      this.map.addControl(zoomControl, kakao.maps.ControlPosition.BOTTOMRIGHT);
      var mapTypeControl = new kakao.maps.MapTypeControl();
      //     
      // kakao.maps.ControlPosition     TOPRIGHT   
      this.map.addControl(
        mapTypeControl,
        kakao.maps.ControlPosition.BOTTOMRIGHT,
      );

      //          
    },
    async getPositions() {
      let gu; //  
      if (this.currentGu != null) {
        this.guList.forEach((data) => {
          if (data.gugunName == this.currentGu) {
            gu = data.gugunCode;
          }
        });
        console.log("currentgu: " + gu);

        await http
          .get(`/map/guApt`, {
            params: {
              gu: gu,
            },
          })
          .then(({ data }) => {
            this.houselist = data;

            //console.log(this.houselist);
          })
          .catch((error) => {
            console.log(error);
          });
      }
      await this.getHouseList(gu);
      //this.getAptListCenter();
      this.aptListToMarkers();
    },
    setCurrentGu() {
      console.log(this.currentGu);
      console.log(latLngs[this.currentGu]);
      this.guSelected = true;
      let moveLatLng = new kakao.maps.LatLng(
        latLngs[this.currentGu][0],
        latLngs[this.currentGu][1],
      );
      this.map.panTo(moveLatLng);
      this.getPositions();
    },
    // async setCurrentGu() {
    //   console.log("setcurrentGu");
    //   this.guSelected = true;
    //   this.getPositions();
    // },
    async getCurrentGu() {
      this.guSelected = true;
      await this.searchAddrFromCoords(
        this.map.getCenter(),
        this.displayCenterInfo,
      );
    },
    // getAptListCenter() {
    //   let maxLng = 0;
    //   let minLng = 180;
    //   let maxLat = 0;
    //   let minLat = 180;
    //   this.houselist.forEach((apt) => {
    //     if (maxLat < apt.lat) maxLat = apt.lat;

    //     if (minLat > apt.lat) minLat = apt.lat;
    //     if (maxLng < apt.lng) maxLng = apt.lng;
    //     if (minLng > apt.lng) minLng = apt.lng;
    //   });
    //   this.aptListCenter = {
    //     lngCenter: (parseFloat(maxLng) + parseFloat(minLng)) / 2,
    //     latCenter: (parseFloat(maxLat) + parseFloat(minLat)) / 2,
    //   };
    //   this.panTo();
    //   // 
    //   /* let sw = new kakao.maps.LatLng(minLat, minLng),
    //     ne = new kakao.maps.LatLng(maxLat, maxLng);
    //   console.log(sw);
    //   let bounds = kakao.maps.LatLngBounds(sw, ne);
    //   this.map.setBounds(bounds);*/
    // },
    // panTo() {
    //   console.log(this.aptListCenter.lngCenter);
    //   //     
    //   var moveLatLon = new kakao.maps.LatLng(
    //     this.aptListCenter.latCenter,
    //     this.aptListCenter.lngCenter,
    //   );

    //   //    
    //   //          
    //   this.map.panTo(moveLatLon);
    // },
    async searchAddrFromCoords(coords, callback) {
      //     
      await this.geocoder.coord2RegionCode(
        coords.getLng(),
        coords.getLat(),
        callback,
      );
    },
    emptyMarkers() {
      // 
      if (this.markers.length > 0) {
        this.markers.forEach((marker) => marker.setMap(null));
      }
    },
    aptListToMarkers() {
      //aptList   

      this.emptyMarkers();
      //this aptList      
      for (let i = 0; i < this.houselist.length; i++) {
        let marker = new kakao.maps.Marker({
          map: this.map,
          position: new kakao.maps.LatLng(
            this.houselist[i].lat,
            this.houselist[i].lng,
          ),
          title: this.houselist[i].aptCode,
        });
        kakao.maps.event.addListener(marker, "click", async () => {
          // 
          this.aptSelected = true;
          //console.log(marker);
          await this.getHouseInfo(marker.getTitle(), marker);
          this.getHouseDealsYearly(marker.getTitle());
          this.getHouseDeals(marker.getTitle());
          /*if (this.infowindow) this.infowindow.close();
          this.infowindow = new kakao.maps.InfoWindow({
            content: this.house.aptName, //   
          });

          this.infowindow.open(this.map, marker);*/
          //console.log(this.housedeals);
        });
        // 
        this.markers.push(marker);
      }
    },
    displayCenterInfo(result, status) {
      if (status === kakao.maps.services.Status.OK) {
        //var infoDiv = document.getElementById("centerAddr");

        // for (var i = 0; i < result.length; i++) {
        //  region_type  'H' 
        // if (result[i].region_type === "H") {
        //   infoDiv.innerHTML = result[i].address_name;
        //   break;
        // }

        //}
        //console.log(result);
        //infoDiv.innerHTML = result[0].region_2depth_name;
        this.currentGu = result[0].region_2depth_name;
        this.currentDong = result[0].region_3depth_name;
        this.getPositions();
      }
    },
    async getHouseList(guCode) {
      console.log("gethouselist :" + guCode);
      await http
        .get(`/map/guApt`, {
          params: {
            gu: guCode,
          },
        })
        .then(({ data }) => {
          this.SET_HOUSE_LIST(data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    async getHouseInfo(aptCode) {
      await http
        .get(`/map/aptDetail`, {
          params: {
            aptCode: aptCode,
          },
        })
        .then(({ data }) => {
          this.SET_DETAIL_HOUSE(data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getHouseDealsYearly(aptCode) {
      http
        .get(`/map/aptDealsYearly`, {
          params: {
            aptCode: aptCode,
          },
        })
        .then(({ data }) => {
          this.SET_HOUSE_DEALS_YEARLY(data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getHouseDeals(aptCode) {
      http
        .get(`/map/aptDeals`, {
          params: {
            aptCode: aptCode,
          },
        })
        .then(({ data }) => {
          this.SET_HOUSE_DEALS(data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    //////////////////////////////////////////////////////       ////////////////
    searchPlaces() {
      if (!this.currCategory) {
        console.log("curr empty");
        return;
      }

      //   
      // this.placeOverlay.setMap(null);

      //     
      this.removeAmenityMarker();

      this.ps.categorySearch(this.currCategory, this.placesSearchCB, {
        useMapBounds: true,
      });
    },
    placesSearchCB(data, status) {
      if (status === kakao.maps.services.Status.OK) {
        //      
        this.displayPlaces(data);
      } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
        //        
      } else if (status === kakao.maps.services.Status.ERROR) {
        //            
      }
    },
    displayPlaces(places) {
      //     
      //       
      var order = document
        .getElementById(this.currCategory)
        .getAttribute("data-order");

      for (var i = 0; i < places.length; i++) {
        //    
        var marker = this.addAmenityMarker(
          new kakao.maps.LatLng(places[i].y, places[i].x),
          order,
        );

        //      
        //     
        ((marker, place) => {
          kakao.maps.event.addListener(marker, "click", () => {
            this.displayPlaceInfo(place);
            console.log(place);
          });
        })(marker, places[i]);
      }
    },
    addAmenityMarker(position, order) {
      var imageSrc =
          "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_category.png", //   url,   
        imageSize = new kakao.maps.Size(27, 28), //   
        imgOptions = {
          spriteSize: new kakao.maps.Size(72, 208), //   
          spriteOrigin: new kakao.maps.Point(46, order * 36), //       
          offset: new kakao.maps.Point(11, 28), //      
        },
        markerImage = new kakao.maps.MarkerImage(
          imageSrc,
          imageSize,
          imgOptions,
        ),
        marker = new kakao.maps.Marker({
          position: position, //  
          image: markerImage,
        });

      marker.setMap(this.map); //    
      this.amenitymarkers.push(marker); //    

      return marker;
    },
    removeAmenityMarker() {
      for (var i = 0; i < this.amenitymarkers.length; i++) {
        this.amenitymarkers[i].setMap(null);
      }
      this.amenitymarkers = [];
      this.SET_DETAIL_AMENITY(null);
    },
    displayPlaceInfo(place) {
      let temp = {
        name: "",
        phone: "",
        address: "",
      };
      temp.name = place.place_name;
      temp.phone = place.phone;
      temp.address = place.road_address_name;

      this.SET_DETAIL_AMENITY(temp);
      console.log(this.amenity);
      // var content =
      //   '<div class="placeinfo">' +
      //   '   <a class="title" href="' +
      //   place.place_url +
      //   '" target="_blank" title="' +
      //   place.place_name +
      //   '">' +
      //   place.place_name +
      //   "</a>";

      // if (place.road_address_name) {
      //   content +=
      //     '    <span title="' +
      //     place.road_address_name +
      //     '">' +
      //     place.road_address_name +
      //     "</span>" +
      //     '  <span class="jibun" title="' +
      //     place.address_name +
      //     '">( : ' +
      //     place.address_name +
      //     ")</span>";
      // } else {
      //   content +=
      //     '    <span title="' +
      //     place.address_name +
      //     '">' +
      //     place.address_name +
      //     "</span>";
      // }

      // content +=
      //   '    <span class="tel">' +
      //   place.phone +
      //   "</span>" +
      //   "</div>" +
      //   '<div class="after"></div>';

      // this.contentNode.innerHTML = content;
      // this.placeOverlay.setPosition(new kakao.maps.LatLng(place.y, place.x));
      // this.placeOverlay.setMap(this.map);
    },
    //     
    addCategoryClickEvent() {
      var category = document.getElementById("category"),
        children = category.children;

      console.log(category);
      console.log(children);

      for (var i = 0; i < children.length; i++) {
        children[i].onclick = this.onClickCategory;
      }
    },
    //     
    onClickCategory(e) {
      var id = e.target.closest("li").id;
      var className = e.target.closest("li").className;
      // this.placeOverlay = new kakao.maps.CustomOverlay({ zIndex: 1 });
      // this.placeOverlay.setMap(null);
      console.log(e.currentTarget);

      if (className === "on") {
        this.currCategory = "";
        this.changeCategoryClass();
        this.removeAmenityMarker();
        //this.searchPlaces();
      } else {
        this.currCategory = id;
        this.changeCategoryClass(e.target.closest("li"));
        this.searchPlaces();
      }
    },
    //      
    changeCategoryClass(el) {
      var category = document.getElementById("category"),
        children = category.children,
        i;

      for (i = 0; i < children.length; i++) {
        children[i].className = "";
      }

      if (el) {
        console.log("", el);
        if (el) el.className = "on";
      }
    },
  },
  watch: {
    center(latlng) {
      console.log("watched");
      this.map.setCenter(new kakao.maps.LatLng(latlng[0], latlng[1]));
      //this.center = nv;
    },
    house(house) {
      console.log("watched");
      if (this.infowindow) this.infowindow.close();
      //console.log(this.markers);
      this.markers.forEach((marker) => {
        if (marker.getTitle() == house.aptCode) {
          console.log(marker);
          this.infowindow = new kakao.maps.InfoWindow({
            content: this.house.aptName, //   
          });

          this.infowindow.open(this.map, marker);
        }
      });
    },
  },
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
      console.log("kakao exist");
    } else {
      console.log("kakao not exist");
      const script = document.createElement("script");
      /* global kakao */
      //console.log(process.env.VUE_APP_MAP_KEY);
      script.onload = () => kakao.maps.load(this.initMap);
      script.src = `//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=${process.env.VUE_APP_MAP_KEY}&libraries=services,clusterer`;
      document.head.appendChild(script);
    }
    // this.SET_DETAIL_AMENITY(null);
    // this.contentNode = document.createElement("div");
    // this.addCategoryClickEvent();
    // kakao.maps.event.addListener(this.map, "idle", this.searchPlaces);
    // var mapTypeControl = new kakao.maps.MapTypeControl();
    // //     
    // // kakao.maps.ControlPosition     TOPRIGHT   
    // this.map.addControl(mapTypeControl, kakao.maps.ControlPosition.BOTTOMRIGHT);

    // //          
    // var zoomControl = new kakao.maps.ZoomControl();
    // this.map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
  },
  created() {
    this.SET_HOUSE_LIST([]);
    this.SET_DETAIL_HOUSE(null);
    for (let i = 0; i < 25; i++) {
      this.options.push(new Object());
      this.options[i].value = Object.keys(latLngs)[i];
      this.options[i].text = Object.keys(latLngs)[i];
      this.guList.push(new Object());
      this.guList[i].gugunName = Object.keys(latLngs)[i];
      this.guList[i].gugunCode = Object.values(latLngs)[i][2];
    }
    // http
    //   .get(`/map/gugun`, {
    //     params: {
    //       sido: "11",
    //     },
    //   })
    //   .then(({ data }) => {
    //     this.guList = data;
    //     for (let i = 0; i < data.length; i++) {
    //       this.options.push(new Object());
    //       this.options[i].value = data[i].gugunName;
    //       this.options[i].text = data[i].gugunName;
    //     }
    //     console.log(this.guList);
    //   })
    //   .catch((error) => {
    //     console.log(error);
    //   });
  },
};
</script>

<style>
#map {
  width: 100%;
  height: 100%;
}
.listclose {
  background-color: rgba(255, 255, 255, 0.8);
  position: relative;
  z-index: 1;
}

.popuparea {
  height: 100%;
  position: absolute;
  top: 0;
  display: flex;
}
.popup {
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 1;
  width: 300px;
  height: 100%;
  position: relative;
}
.amenitypopup {
  height: 100%;
  position: absolute;
  top: 100px;
  right: 50px;
  display: flex;
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 1;
  width: 300px;
  height: 300px;
}

.mapwrap {
  height: 100%;
  width: 100%;
  position: relative;
}
.gu-select {
  position: absolute;
  top: 20px;
  display: flex;
  z-index: 1;
  margin-left: 50%;
}

.markerbtn {
  white-space: nowrap;
  margin-left: 0.4rem;
}

#category {
  position: absolute;
  top: 10px;
  right: 10px;
  border-radius: 5px;
  border: 1px solid #909090;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.4);
  background: #fff;
  overflow: hidden;
  z-index: 2;
  padding-left: 0px;
}
#category li {
  float: left;
  list-style: none;
  width: 60px;
  border-right: 1px solid #acacac;
  padding: 6px 0;
  text-align: center;
  cursor: pointer;
}
#category li.on {
  background: #eee;
}
#category li:hover {
  background: #ffe6e6;
}
#category li:last-child {
  margin-right: 0;
  border-right: 0;
}
#category li span {
  display: block;
  margin: 0 auto 3px;
  width: 27px;
  height: 28px;
}
#category li .category_bg {
  background: url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_category.png)
    no-repeat;
}
#category li .bank {
  background-position: -10px 0;
}
#category li .mart {
  background-position: -10px -36px;
}
#category li .pharmacy {
  background-position: -10px -72px;
}
#category li .oil {
  background-position: -10px -108px;
}
#category li .cafe {
  background-position: -10px -144px;
}
#category li .store {
  background-position: -10px -180px;
}
#category li.on .category_bg {
  background-position-x: -46px;
}
</style>
